/* Swiper override */
.swiper-pagi {
  font-size: 16px;
  color: #ffffff;
}

.swiper-pagination-current {
  font-size: 32px;
}

.swiper-prev,
.swiper-next {
  width: 64px;
  height: 64px;
  background-color: #ffffff;
  border: 1px solid rgba(166, 166, 166, 0.7);
  border-radius: 50%;
  background-repeat: no-repeat;
  background-position: center center;
  box-sizing: border-box;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
@media only screen and (max-width: 767px) {
  .swiper-prev,
.swiper-next {
    width: 48px;
    height: 48px;
  }
}
@media (min-width: 1080px) {
  .swiper-prev:hover, .swiper-prev:focus,
.swiper-next:hover,
.swiper-next:focus {
    background-color: rgba(255, 255, 255, 0.9);
  }
}

.swiper-prev {
  margin-right: 8px;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='16' height='8' viewBox='0 0 16 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M15 4H1M1 4L4 1M1 4L4 7' stroke='%230E0E0E' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/svg%3e");
}

.swiper-next {
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='16' height='8' viewBox='0 0 16 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M1 4H15M15 4L12 1M15 4L12 7' stroke='%230E0E0E' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/svg%3e");
}

